export default {
	props:{
		shareMsg: Object
	},
	
	computed:{
		indicatorDots(){
			return this.shareMsg.images.length > 1;
		}
	},
	
	data() {
		return {
			current: 0
		};
	},
	
	methods:{
		onChangeCurrent(e){
			this.current = e.detail.current;
		},
		onPreview(){
			const { images } =this.shareMsg;
			uni.previewImage({
				current: this.current,
				urls: images
			});
		},
		onLike(){
			console.log('onLike',this.shareMsg)
		},
		onShare(){
			console.log('onShare',this.shareMsg)
		}
	}
	
}
